"use client"
import { BaggageClaim, BarChart4, Cable, ChevronLeft, Home, PlusCircle, ScrollText, ShoppingBag, ShoppingCart } from 'lucide-react'
import Link from 'next/link'
import React, { useState } from 'react'
import Subscription from './Subscription'
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible"
import CollapsibleItems from './CollapsibleItems'


function Sidebar() {

  const items = [
    {
      title: 'Home',
      href: '#',
      isCollapsible: false,
    },
    {
      title: 'Inventory',
      href: '#',
      isCollapsible: true,
      collapsibles: [
        {
          title: 'Purchase Item 1',
          href: '#',
        }
      ]
    },
    {
      title: 'Sales',
      href: '#',
      isCollapsible: true,
      collapsibles: [
        {
          title: 'Purchase Item 1',
          href: '#',
        }
      ]
    },
    {
      title: 'Purchases',
      href: '#',
      isCollapsible: true,
      collapsibles: [
        {
          title: 'Purchase Item 1',
          href: '#',
        }
      ]
    },
    {
      title: 'Integrations',
      href: '#',
      isCollapsible: false,
    },
    {
      title: 'Reports',
      href: '#',
      isCollapsible: false,
    },
    {
      title: 'Documentation',
      href: '#',
      isCollapsible: false,
    }
  ]

  const [active, setActive] = useState({
    inventory: false,
    item: false,
  });

  const handleItemClick = () => {
    setActive({
      inventory: true, 
      item: !active.item,
    });
  };

  const handleInventoryClick = () => {
    setActive({
      inventory: !active.inventory,
      item: false,
    });
  }

  return (
<div>
  <div className="w-60 min-h-screen bg-slate-800 text-slate-50 fixed flex flex-col justify-between">
    {/* Top Part */}
    <div>
      {/* Logo */}
      <Link href="#" className="bg-slate-900 border-b h-[6.6vh] border-slate-900 shadow-slate-400 flex space-x-2 items-center justify-center pt-3 pb-3 px-2">
        <ShoppingCart />
        <span className="text-xl font-semibold">Inventory</span>
      </Link>
      {/* Links */}

      <nav className="flex flex-col gap-3 px-3 py-6">
            {
              items.map((item, i) => {
                // Check for the condition before rendering the link
                if (!item.isCollapsible && !item.collapsibles) {
                  return null;  // Skip the link if it's not collapsible or doesn't have collapsible items
                }

                return (
                  <div key={i}>
                    <Link href={item.href} className="flex items-center p-3 space-x-2 text-slate-50 rounded-md">
                      {item.title === 'Home' ? (
                        <Home className="w-4 h-4" />
                      ) : (
                        <BaggageClaim className="w-4 h-4" />
                      )}
                      <span>{item.title}</span>
                    </Link>

                    {item.isCollapsible && (
                      <CollapsibleItems
                        collapsibles={item.collapsibles}
                        active={active}
                        setActive={setActive}
                        handleItemClick={handleItemClick}
                        handleInventoryClick={handleInventoryClick}
                      />
                    )}
                  </div>
                )
              })
            }
          </nav>
    </div>

    {/* Bottom Part */}
    <div>
      {/* Subscription Card */}
      <Subscription />
      {/* Footer Icon */}
      <div className="bg-slate-950 flex space-x-2 justify-center pt-3 pb-3 px-2">
        <ChevronLeft />
      </div>
    </div>
  </div>
</div>
  )
}

export default Sidebar